/* General page layout */
.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #30bfbf;
}

/* Container for the text areas and buttons */
.textarea-container {
  display: flex;
  gap: 20px;
  margin: 20px 0;
  width: 80%;
  /* Adjusted width */
  max-width: 1200px;
  /* Optional: maximum width for the content */
}

/* Styling each text area */
.textarea-section {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* Fill the available space */
}

/* Input areas */
textarea {
  width: 100%;
  height: 150px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  font-family: 'Courier New', Courier, monospace;
  box-sizing: border-box;
  /* Ensure padding is considered inside the total width */
}

/* Read-only text areas */
textarea.readonly {
  background-color: #f4f4f4;
  cursor: not-allowed;
}

/* Button styling */
.button-container {
  display: flex;
  gap: 10px;
  margin: 10px 0;
}

button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

/* Header Styling */
h1 {
  font-size: 1.5rem;
  color: white;
  margin-bottom: 20px;
}